<template>
  <b-container fluid>
    <ValidationObserver v-slot="{ handleSubmit }">
      <form @submit.prevent="handleSubmit(onSubmit)">
        <b-row>
          <b-col lg="3">
            <iq-card>
              <template v-slot:body>
                <b-form-group class="profile-box d-flex flex-column align-items-center">
                  <div class="add-img-user profile-img-edit">
                    <b-img class="profile-pic height-150 width-150" fluid :src="user.profile_image" alt="profile-pic" />
                  </div>
                  <div class="title text-center font-size-24">{{ user.fullName }}</div>
                </b-form-group>
              </template>
            </iq-card>
          </b-col>
          <b-col lg="9">
            <iq-card>
              <template v-slot:headerTitle>
                <h4 class="card-title">Profile Detail</h4>
              </template>
              <template v-slot:body>
                <div class="alert alert-danger" v-if="updateError">An error has occurred, please make sure you fill in your information completely and correctly!</div>
                <div class="alert alert-success" v-if="updateSuccess">Your information has been updated successfully!</div>
                <div class="new-user-info">
                  <b-row>
                    <b-form-group class="col-md-6" label="User Name:" label-for="uname">
                      <ValidationProvider name="User Name" rules="required" v-slot="{ errors }">
                        <b-form-input v-model="user.fullName" type="text" placeholder="User Name" :class="(errors.length > 0 ? ' is-invalid' : '')"></b-form-input>
                        <div class="invalid-feedback">
                          <span>{{ errors[0] }}</span>
                        </div>
                      </ValidationProvider>
                    </b-form-group>
                    <b-form-group class="col-md-6" label="Email:" label-for="uname">
                      <ValidationProvider name="Email" rules="required|email" v-slot="{ errors }">
                        <b-form-input v-model="user.email" type="text" placeholder="Email" :class="(errors.length > 0 ? ' is-invalid' : '')"></b-form-input>
                        <div class="invalid-feedback">
                          <span>{{ errors[0] }}</span>
                        </div>
                      </ValidationProvider>
                    </b-form-group>
                    <b-form-group class="col-md-6" label="Identification Number:" label-for="uname">
                      <ValidationProvider name="User Name" rules="required" v-slot="{ errors }">
                        <b-form-input v-model="user.identificationNumber" @keypress="isNumber($event)" type="text" placeholder="Identification Number" :class="(errors.length > 0 ? ' is-invalid' : '')"></b-form-input>
                        <div class="invalid-feedback">
                          <span>{{ errors[0] }}</span>
                        </div>
                      </ValidationProvider>
                    </b-form-group>
                    <b-form-group class="col-md-6" label="Mobile Number:" label-for="mobno">
                      <ValidationProvider name="Mobile Number" rules="required" v-slot="{ errors }">
                        <b-form-input v-model="user.mobile_no" @keypress="isNumber($event)" type="text" placeholder="Mobile Number" :class="(errors.length > 0 ? ' is-invalid' : '')"></b-form-input>
                        <div class="invalid-feedback">
                          <span>{{ errors[0] }}</span>
                        </div>
                      </ValidationProvider>
                    </b-form-group>
                    <b-form-group class="col-md-6" label="Country:" label-for="country">
                      <ValidationProvider name="Country" rules="required" v-slot="{ errors }">
                        <b-select v-model="selectedCountry" :class="(errors.length > 0 ? ' is-invalid' : '')">
                          <b-select-option v-for="i in country" :key="i.id" :value="i.id" :selected="i.id == selectedCountry ? true : false">{{ i.name }}</b-select-option>
                        </b-select>
                        <div class="invalid-feedback">
                          <span>{{ errors[0] }}</span>
                        </div>
                      </ValidationProvider>
                    </b-form-group>
                    <b-form-group class="col-md-6" label="City:" label-for="city">
                      <ValidationProvider name="Cities" rules="required" v-slot="{ errors }">
                        <b-select v-model="user.selectedCity" :class="(errors.length > 0 ? ' is-invalid' : '')">
                          <b-select-option v-for="i in cities" :key="i.id" :value="i.name" :selected="i.name == user.selectedCity ? true : false">{{ i.name }}</b-select-option>
                        </b-select>
                        <div class="invalid-feedback">
                          <span>{{ errors[0] }}</span>
                        </div>
                      </ValidationProvider>
                    </b-form-group>
                    <b-form-group class="col-md-6" label="Password:" label-for="pass">
                      <ValidationProvider name="Password" rules="required|confirmed:repeat_password" v-slot="{ errors }">
                        <b-form-input v-model="user.password" type="password" placeholder="Password" :class="(errors.length > 0 ? ' is-invalid' : '')"></b-form-input>
                        <div class="invalid-feedback">
                          <span>{{ errors[0] }}</span>
                        </div>
                      </ValidationProvider>
                    </b-form-group>
                    <b-form-group class="col-md-6" label="Repeat Password:" label-for="rpass">
                      <ValidationProvider vid="repeat_password" name="Repeat Password" rules="required" v-slot="{ errors }">
                        <b-form-input v-model="user.repeat_password" type="password" placeholder="Repeat Password" :class="(errors.length > 0 ? ' is-invalid' : '')"></b-form-input>
                        <div class="invalid-feedback">
                          <span>{{ errors[0] }}</span>
                        </div>
                      </ValidationProvider>
                    </b-form-group>
                  </b-row>
                  <b-button variant="primary" type="submit">Save</b-button>
                </div>
              </template>
            </iq-card>
          </b-col>
        </b-row>
      </form>
    </ValidationObserver>
  </b-container>
</template>
<script>
import { core } from '../../config/pluginInit'
import api from '@/router/api'

export default {
  name: 'AddUser',
  data () {
    return {
      updateError: false,
      updateSuccess: false,
      selectedCountry: this.$store.state.Setting.authUser.user.country,
      country: [],
      cities: [],
      user: {
        id: this.$store.state.Setting.authUser.user.id,
        fullName: this.$store.state.Setting.authUser.user.fullName,
        email: this.$store.state.Setting.authUser.user.email,
        password: '',
        repeat_password: '',
        selectedCity: this.$store.state.Setting.authUser.user.city,
        profile_image: require('../../assets/images/user/user-11.png'),
        mobile_no: this.$store.state.Setting.authUser.user.phone,
        identificationNumber: this.$store.state.Setting.authUser.user.identificationNumber
      }
    }
  },
  methods: {
    onSubmit () {
      api.post('/users/userUpdate', {
        id: this.user.id,
        fullName: this.user.fullName,
        email: this.user.email,
        password: this.user.password,
        phone: this.user.mobile_no,
        country: this.selectedCountry,
        city: this.user.selectedCity,
        identificationNumber: this.user.identificationNumber
      }).then(res => {
        if (Number(res.data.status) === 1) {
          this.updateSuccess = true
          setTimeout(() => {
            this.updateSuccess = false
          }, 5000)
        } else {
          this.updateError = true
          this.user.password = ''
          this.user.repeat_password = ''
          setTimeout(() => {
            this.updateError = false
          }, 5000)
        }
      }).catch(() => {
        this.updateError = true
        this.user.password = ''
        this.user.repeat_password = ''
        setTimeout(() => {
          this.updateError = false
        }, 5000)
      })
    },
    previewImage: function (event) {
      const input = event.target

      if (input.files && input.files[0]) {
        const reader = new FileReader()

        reader.onload = (e) => {
          this.user.profile_image = e.target.result
        }

        reader.readAsDataURL(input.files[0])
      }
    },
    isNumber: function (evt) {
      evt = evt || window.event
      const charCode = evt.which ? evt.which : evt.keyCode
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== 46
      ) {
        evt.preventDefault()
      } else {
        return true
      }
    },
    getCities () {
      const cities = require('./cities.json')
      cities.forEach(city => {
        if (Number(this.selectedCountry) === Number(city.country_id)) {
          this.cities.push(city)
        }
      })
    }
  },
  watch: {
    selectedCountry () {
      this.cities = []
      const Allcities = require('./cities.json')
      Allcities.forEach(item => {
        if (Number(this.selectedCountry) === Number(item.country_id)) {
          this.cities.push(item)
        }
      })
    }
  },
  created () {
    this.country = require('./countries.json')
    if (this.selectedCountry.length > 0) {
      this.getCities()
    }
  },
  mounted () {
    core.index()
  }

}
</script>

<style scoped lang="scss">
#title{
  font-size: 35px !important;
}
</style>
